import { Injectable } from '@angular/core';
import * as io from 'socket.io-client';
import { Observable } from 'rxjs/Observable';
import * as Rx from 'rxjs/Rx';
import { environment } from '../environments/environment';
import * as kurentoUtils from 'kurento-utils';


@Injectable({
  providedIn: 'root'
})
export class WebSocketService {

  // private webSocket;
  private ws = new WebSocket('wss://' + location.host + '/player');
  private webRtcPeer: kurentoUtils.WebRtcPeer;

  constructor() { }


  connect(video: HTMLVideoElement) {

    this.start(video);

    this.ws.onmessage = (message) => {
      let parsedMessage = JSON.parse(message.data);
      console.info('Received message: ' + message.data);

      switch (parsedMessage.id) {
        case 'startResponse':
          this.startResponse(parsedMessage);
          break;
        case 'error':
          console.error('Error message from server: ' + parsedMessage.message);
          break;
        case 'playEnd':
          console.info('Play End');
          break;
        case 'iceCandidate':
          this.webRtcPeer.addIceCandidate(parsedMessage.candidate, function(error) {
            if (error) {
              return console.error('Error adding candidate: ' + error);
            }
          });
          break;
        case 'msgConnInfo':
          console.info('msgConnInfo: ' + parsedMessage.text);
          break;
        case 'msgSdpText':
          console.info('msgSdpText: ' + parsedMessage.text);
          break;
        default:
          console.error('Unrecognized message', parsedMessage);
          break;
      }
    };
  }

  start(video) {
    console.info('[start] Create WebRtcPeer');

    let options = {
      remoteVideo: video,
      mediaConstraints: { audio: false, video: true },
      onicecandidate: this.onIceCandidate
    };

    this.webRtcPeer = kurentoUtils.WebRtcPeer.WebRtcPeerRecvonly(options, (error) => {
        if (error) {
          return console.error(error);
        }

        console.info('[WebRtcPeer] Generate SDP Offer');
        this.webRtcPeer.generateOffer( (err, offerSdp) => {
          if (err) {
            return console.error('Error generating the SDP Offer');
          }

          let message = {
            id: 'start',
            sdpOffer: offerSdp,
            useComedia: true,
            useSrtp: false
          };

          console.info('[onOffer] Received SDP Offer; send message to Kurento Client at ' + location.host);
          console.info('[onOffer] COMEDIA checkbox is: ' + message.useComedia);
          console.info('[onOffer] SRTP checkbox is: ' + message.useSrtp);
          // console.log('[sendMessage] : ', message);
          this.sendMessage(message);
        });
      });
  }

  stop = () => {
    console.info('[stop] Stop video playback');

    if (this.webRtcPeer) {
      this.webRtcPeer.dispose();
      this.webRtcPeer = null;

      let message = {
        id: 'stop'
      };
      // console.log('[sendMessage] : ', message);
      this.sendMessage(message);
    }
  }


  startResponse = (message) => {
    console.info('[startResponse] SDP Answer received from Kurento Client; process in WebRtcPeer');

    this.webRtcPeer.processAnswer(message.sdpAnswer, function(error) {
      if (error) {
        return console.error(error);
      }
    });
  }

  onIceCandidate = (candidate) => {
    console.log('[onIceCandidate] Local candidate: ' + JSON.stringify(candidate));

    let message = {
      id: 'onIceCandidate',
      candidate: candidate
    };
    // console.log('[sendMessage] : ', message);
    this.sendMessage(message);
  }

  onOffer = (error, offerSdp) => {
    if (error) {
      return console.error('Error generating the SDP Offer');
    }

    let message = {
      id: 'start',
      sdpOffer: offerSdp,
      useComedia: true,
      useSrtp: false
    };

    console.info('[onOffer] Received SDP Offer; send message to Kurento Client at ' + location.host);
    console.info('[onOffer] COMEDIA checkbox is: ' + message.useComedia);
    console.info('[onOffer] SRTP checkbox is: ' + message.useSrtp);
    // console.log('[sendMessage] : ', message);
    this.sendMessage(message);
  }

  sendMessage(message) {
    console.info('[sendMessage] message: ' + message);
    console.log(message);

    let jsonMessage = JSON.stringify(message);
    this.ws.send(jsonMessage);
  }

}
